  --color-bg: #433d3c;
  --color-bg-variant: #944e6c;
  --color-primary: #e9c496;
  --color-primary-variant: #83a95c;

  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Montserrat", sans-serif;
  background: #433d3c;
  background: var(--color-bg);
  color: #fff;
  color: var(--color-white);
  line-height: 1.7;
}

.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

section > h2 {
  color: #e9c496;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

a {
  color: #e9c496;
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #e9c496;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #e9c496;
  border: 1px solid var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

.btn:hover {
  background: #fff;
  background: var(--color-white);
  color: #433d3c;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: #e9c496;
  background: var(--color-primary);
  color: #433d3c;
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* MEDIA QUERIES */

@media screen and (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

@media screen and (max-width: 600px) {
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }

  section {
    margin-bottom: 2rem;
  }
}

